.header__button {
  display: none;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
  vertical-align: middle;
}

.header__button:hover {
  opacity: 0.6;
}

.header__button:active {
  opacity: 0.3;
}

@media (min-width: 320px) {
  .header__button {
    display: block;
    position: relative;
    width: 22px;
    height: 22px;
    z-index: 10;
  }

  .header__button::after,
.header__button::before {
    content: "";
    position: absolute;
    background-color: #81b3d2;
    width: 100%;
    height: 4px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .header__button-line {
    content: "";
    position: absolute;
    background-color: #81b3d2;
    width: 100%;
    height: 4px;
    left: 0;
    top: 9px;
  }

  .header__button::before {
    top: 0;
  }

  .header__button::after {
    bottom: 0;
  }

  .header__button--active {
    background-color: transparent;
  }

  .header__button--active .header__button-line {
    transform: scale(0);
    transition: all 0.3s ease 0s;
  }

  .header__button--active::before {
    transform: rotate(45deg);
    background-color: #ffffff;
    top: 9px;
  }

  .header__button--active::after {
    transform: rotate(-45deg);
    background-color: #ffffff;
    bottom: 9px;
  }
}
@media (min-width: 767px) {
  .header__button {
    display: none;
  }
}
